@import "../../theme.scss";

.Setrahmen-moudle,
.frameuser {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.2);
  z-index: 99;
  text-align: center;

  &::after {
    content: "";
    display: inline-block;
    height: 100%;
    vertical-align: middle;
  }

  .Setrahmen-moudle-boby,
  .frameuser-inner {
    display: inline-block;
    width: 70vw;
    min-width: 585px;
    background: rgba(255, 255, 255, 1);
    box-shadow: 0px 4px 12px 0px rgba(0, 0, 0, 0.2);
    vertical-align: middle;

    .Setrahmen-hd,
    .frameuser-hd {
      padding: 16px 24px;
      border-bottom: 1px solid #e9e9e9;
      text-align: left;

      .hd-title,
      i {
        font-size: 18px;

        color: rgba(0, 0, 0, 1);
        line-height: 24px;
      }

      .hd-close-icon {
        float: right;
        cursor: pointer;
      }
    }

    .Setrahmen-sort {
      height: 40px;
    }

    .SetrahmencheckAll {
      text-align: left;
      line-height: 25px;
      margin-left: 10px;

      i {
        margin-right: 10px;
        cursor: pointer;
      }
    }

    .Setrahmen-bd {
      height: 60vh;
      text-align: left;
      overflow-y: auto;
      background-color: #e9e9e9;
      display: flex;
      flex-wrap: wrap;
      align-items: flex-start;

      .list-box {
        position: relative;
        width: 190px;
        background-color: #fff;
        margin: 7px;
        border: 2px solid #fff;
        border-radius: 4px;
        cursor: pointer;

        .list-box-check {
          position: absolute;
          display: block;
          top: 5px;
          right: 5px;
          width: 20px;
          height: 20px;
          background: rgba(255, 255, 255, 1);
          border-radius: 3px;
          border: 2px solid rgba(175, 175, 175, 1);

          i {
            vertical-align: top;
            color: #fff;
            visibility: hidden;
          }
        }

        .default {
          position: absolute;
          display: block;
          width: 44px;
          right: 5px;
          top: 5px;
          height: 18px;
          background: $bg-color;
          border-radius: 3px;
          font-size: 12px;
          color: white;
          line-height: 18px;
          text-align: center;
        }

        .top-img {
          .img1 {
            height: 80px;
            background: url("../../images/imgerror.png") no-repeat;
            background-position: center center;
            background-size: 100% 130%;
            background-color: #f3f5f7;
            margin-bottom: 1px;
            text-align: center;

            img {
              height: 100%;
              width: 100%;
              object-fit: cover;
              border-radius: 2px 2px 0 0;
            }
          }

          .img2-4 {
            display: flex;
            height: 38px;
            justify-content: space-around;

            .img2 {
              flex: 30%;
              background: url("../../images/imgerror.png") no-repeat;
              background-color: #f3f5f7;
              background-size: 100% 100%;
              background-position: center center;

              img {
                height: 100%;
                width: 100%;
                object-fit: cover;
              }
            }

            .img3 {
              flex: 30%;
              background: url("../../images/imgerror.png") no-repeat;
              background-color: #f3f5f7;
              background-size: 100% 100%;
              background-position: center center;
              margin: 0 1px;

              img {
                height: 100%;
                width: 100%;
                object-fit: cover;
              }
            }

            .img4 {
              flex: 30%;
              background: url("../../images/imgerror.png") no-repeat;
              background-color: #f3f5f7;
              background-size: 100% 100%;
              background-position: center center;

              img {
                height: 100%;
                width: 100%;
                object-fit: cover;
              }
            }
          }
        }

        .bottom-text {
          position: relative;
          padding: 0 10px;

          .playlist-name {
            width: 115px;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            height: 19px;
            font-size: 14px;

            color: rgba(0, 0, 0, 1);
            line-height: 19px;
            margin: 11px 0;
          }

          .update-time {
            overflow: hidden;
            margin-bottom: 10px;

            span {
              display: block;
              float: left;
              height: 21px;
              background: rgba(243, 243, 243, 1);
              border-radius: 3px;
              font-size: 12px;

              font-weight: 400;
              color: rgba(144, 144, 144, 1);
              line-height: 19px;
              padding: 0 7px;
            }
          }

          .playlist-photo-num {
            position: absolute;
            top: 0;
            right: 10px;
            font-size: 12px;

            font-weight: 400;
            color: rgba(0, 0, 0, 1);
          }
        }
      }

      .list-checked {
        border: 2px solid $bg-color;

        .list-box-check {
          background: $bg-color;
          border-color: $bg-color;

          i {
            visibility: initial;
          }
        }
      }
    }

    .Setrahmen-ft,
    .frameuser-ft {
      text-align: right;
      padding: 22px 24px;
      border-top: 1px solid #e9e9e9;

      .cancel-btn,
      .confirm-btn {
        display: inline-block;
        padding: 0 33px;
        height: 40px;
        font-size: 14px;

        font-weight: 400;
        color: #0a0a0a;
        line-height: 40px;
        border-radius: 3px;
        border: 1px solid #c2c4c7;
        cursor: pointer;
      }

      .confirm-btn {
        margin-left: 24px;
        background: $bg-color;
        color: white;
      }
    }

    .frameuser-bd {
      height: 60vh;
      min-height: 370px;
      text-align: left;
      overflow: auto;

      ul {
        padding: 0 27px;

        li {
          border-bottom: 1px solid #e9e9e9;
          height: 59px;
          display: flex;
          align-items: center;
        }
      }

      .user-icon {
        display: inline-block;
        width: 40px;
        height: 40px;
        background-color: #e9e9e9;
        border-radius: 50%;
        text-align: center;
        border: 1px solid #e9e9e9;

        &::after {
          content: "";
          display: inline-block;
          height: 100%;
          vertical-align: middle;
        }

        i {
          display: inline-block;
          font-size: 20px;
          vertical-align: middle;
        }
      }

      .user-name {
        display: block;
        width: 130px;
        margin-left: 10px;
        font-size: 14px;
        font-weight: 400;
        color: rgba(25, 25, 25, 1);
        line-height: 20px;
      }

      .user-is-admin {
        margin-left: 15px;
        display: inline-block;
        width: 50px;
        height: 24px;
        background: $hover;
        border-radius: 3px;
        font-size: 12px;
        color: $bg-color;
        line-height: 24px;
        text-align: center;
        vertical-align: middle;
      }

      .frameuser-operation {
        margin-left: auto;
        height: 100%;

        .user-delete,
        .user-rename,
        .operation-admin {
          margin: 0 5px;
          font-size: 13px;
          font-weight: 400;
          color: rgba(25, 25, 25, 1);
          line-height: 56px;
          border-radius: 3px;
          padding: 5px 5px;
          border: 1px solid #bfbfbf;
          cursor: pointer;

          &:hover {
            background: $hover;
          }
        }
      }

      // ul li:hover .frameuser-operation {
      //   display: block;
      // }
    }
  }
  .icon-xuanzhong1{
    color:$bg-color;
  }
}