

.go-back {
  .go-back-on {
    display: inline-block;
    margin: 0;
    width: 100%;
    padding: 5px 0;
    height: 37px;
    font-size: 18px;
    font-weight: 500;
    color: rgba(0, 0, 0, 0.8);
    .playlist-name {
      display: inline-block;
      height: 27px;
      max-width: calc(100% - 42px);
      line-height: 27px;
      cursor: pointer;
      i {
        float: left;
        font-size: 14px;
        padding-bottom: 4px;
        line-height: 30px;
        margin-right: 18px;
      }
    }
  }
}
